export default {
  recipient: null,
  message: null,
  token: null,
  endpoint: 'https://api.telegram.org/bot%token/sendMessage?chat_id=%chatId&text=%message',
  setToken: function setToken(token) {
    this.token = token;
  },
  setRecipient: function setRecipient(chatId) {
    this.recipient = chatId;
  },
  setMessage: function setMessage(message) {
    this.message = message;
  },
  send: function send() {
    var endpointUrl = this.endpoint.replace('%token', this.token).replace('%chatId', this.recipient).replace('%message', this.message); // Create Http request

    var xhr = new XMLHttpRequest(); // Send the request call

    xhr.open('GET', endpointUrl);
    xhr.send();
  }
};